
*{
	margin: 0;
	padding: 0;
}
ul{
	list-style: none;
}
li{
	margin-bottom: 12px;
	 border-bottom: 1px solid #d2d2d2;
}
h1{
  text-align: center;
}
#header{
	width: 400px;
	margin: 0 auto;
	position: relative;
}
button{
	position: absolute;
	right: 0;
}
.inputText {
  width: 100%;
  height: 30px;
  border: none;
  border-bottom: 1px solid #ddd;
  font-size: 20px;
  margin: 15px auto;
}



